.startRating {
  display: inline-block;
  overflow: hidden;
  height: 60px;
  width: 300px;
}

.startRating::after{
  content: "";
  display: block;
  position: relative;
  z-index: 10;
  height: 60px;
  background: url("data:image/png;base64,")
  repeat-x 0 0;
  background-size: contain;
  pointer-events: none;
}

.starRating_fill{
  width: 300px;
  background-color: #0084ff;
  position: relative;
  z-index: 1;
  float: left;
  height: 60px;
}