.blind {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: none;
  clip: rect(0, 0, 0, 0);
}

.startRadio {
  display: inline-block;
  overflow: hidden;
  height: 40px;
}

.startRadio_box{
  position: relative;
  z-index: 1;
  float: left;
  width: 20px;
  height: 40px;
  cursor: pointer;
}

.startRadio_box input {
  opacity: 0 !important;
  height: 0 !important;
  width: 0 !important;
  position: absolute !important;
}

.startRadio_box input:checked + .startRadio_img{
  background-color: #0084ff;
}

.startRadio::after{
  content: "";
  display: block;
  position: relative;
  z-index: 10;
  height: 40px;
  background: url("data:image/png;base64,")
    repeat-x 0 0;
  background-size: contain;
  pointer-events: none;
}

.startRadio_img{
  display: block;
  position: absolute;
  right: 0;
  width: 500px;
  height: 40px;
  pointer-events: none;
}